<template>
        <div class="col-md-5 col-sm-5 col-xs-5">
            <div class="dataTables_paginate paging_simple_numbers" id="datatable-fixed-header_paginate">
                <ul class="pagination" style="margin:0" v-if="page.count">
                    <li class="paginate_button previous"   v-bind:class="{ hasPrevPage: 'disabled' }" id="datatable-fixed-header_previous">
                    <a @click="firstPage()"  aria-controls="datatable-fixed-header"  data-dt-idx="0" tabindex="0">First</a></li>
                    <li class="paginate_button" v-bind:class="{ hasPrevPage: 'disabled' }"><a @click="prevPage()" aria-controls="datatable-fixed-header" data-dt-idx="2" tabindex="0">Previous</a></li>
                    <li class="paginate_button" v-bind:class="{hasNextPage: 'disabled' }"><a  @click="nextPage()" aria-controls="datatable-fixed-header" data-dt-idx="3" tabindex="0">Next</a></li>
                    <li class="paginate_button next"  id="datatable-fixed-header_next" v-bind:class="{ hasNextPage: 'disabled' }"><a @click="lastPage()"  aria-controls="datatable-fixed-header" data-dt-idx="7" tabindex="0">Last</a></li>
                </ul>
            </div>
        </div>
</template>
<style>

</style>
<script>
    export default{
       props:['page'],
       created:function()
       {
       },
       methods:
       {
            firstPage:function()
            {
             this.$emit('paginationClick',0)
            },
            lastPage:function()
            {
               this.$emit('paginationClick',this.page.totalPage-1)
            },
            prevPage:function()
            {
               this.$emit('paginationClick',this.page.pn-1);
            },
            nextPage:function()
            {
               this.$emit('paginationClick',this.page.pn+1);
            }
       },
       computed: {
          hasPrevPage: function () {
            try
            {
            let rs =  this.page && this.page.pn && this.page.pn>0;
            console.log("rs:"+rs)
            }
            catch (e)
            {
            console.log(e);
            }
          },
          hasNextPage:function()
          {
            return this.page && this.page.totalPage && this.page.totalPage>0 && this.page.pn<(this.page.totalPage-1);
          }
        }
    }
</script>



