<template>
    <div class="grid">
        <table class="table table-bordered" id="rpt">
            <thead>
                <tr class="headings">
                    <th rowspan="2">ContractId</th>
                    <th rowspan="2">Accum Progress</th>
                    <th rowspan="2">Customer Name</th>
                    <th rowspan="2">Project Name</th>
                    <th rowspan="2">Project Id</th>
                    <th rowspan="2">PM</th>
                    <th v-bind:colspan="milestones.length" >Man-days by Milestone</th>
                    <th v-for="(month,index) in monthWeeks" v-bind:colspan="month" >{{getMonthName(index)}}</th>
                </tr>
                <tr class="headings">
                    <th v-for="milestone in milestones">{{milestone.name}}</th>
                    <template v-for="month in monthWeeks">
                        <th v-for="n in month">W{{ n }}</th>
                    </template>
                </tr>
            </thead>
            <tbody>
                <template v-for="project in projects">
                    <tr>
                        <td>{{project.contractId}}</td>
                        <td>{{project.progress}}%</td>
                        <td>{{project.customerName}}</td>
                        <td>{{project.projectName}}</td>
                        <td>{{project.projectId}}</td>
                        <td>{{project.pm}}</td>
                        <template v-for="milestone in milestones">
                            <td>{{getProjMsManday(project,milestone)}}</td>
                        </template>
                        <template v-for="(month,index) in monthWeeks">
                            <template v-for="n in month">
                                <td>{{getProjWeekManday(project,index+1,n)}}</td>
                            </template>
                        </template>
                    </tr>
                </template>
            </tbody>
       </table>
    </div>
</template>

<script>
import moment from 'moment'
const mm = ["Jan","Feb","Mar","Apr","May","Jun","Jul","Aug","Sep","Oct","Nov","Dec"];
export default {
  data () {
     return {
       projects:[],
       monthWeeks:[],
       milestones:[],
       year:null
    }
  },
  created:function(){
      this.year = moment().format('YYYY');
      let _self = this;
      $.post("/rpt/milestoneRpt",{year:this.year}).done((data)=>{
        _self.projects=data.rs;_self.milestones=data.milestones;_self.monthWeeks=data.mw;
        console.log(_self.milestones);
        }).fail(function(){});
  },
  methods:{
    getMonthName:function(month)
    {
        return mm[month];
    },
    getProjMsManday:function(project,milestone)
    {
        if (project.ms && project.ms[milestone.name])
            return project.ms[milestone.name];
         else
            return "";
    },
    getProjWeekManday:function(project,month,week)
    {
        if (project.ws[month.toString()] && project.ws[month.toString()][week.toString()])
        {
            return project.ws[month.toString()][week.toString()];
        }
        else
         {
            return "";
         }
    }
  }
}
</script>
<style>
.table>thead>tr>th{text-align:center; vertical-align:middle}
</style>



