<template>
    <div class="row">

        <div class="col-md-12 col-sm-12 col-xs-12">
            <div class="x_panel">
                <div class="x_title">
                    <h2>Record Edit<small>edit entry record</small></h2>
                    <ul class="nav navbar-right panel_toolbox">

                    </ul>
                    <div class="clearfix"></div>
                </div>
                <div class="x_content">
                    <br />
                    <form  name="roleForm" id="roleForm"  class="form-horizontal form-label-left"   @submit.prevent="saveRecord">

                        <div class="form-group">
                            <label class="control-label col-md-3 col-sm-3 col-xs-12" >User Id
                            </label>
                            <div class="col-md-6 col-sm-6 col-xs-12">
                                <label class="control-label" >{{record.person}}</label>
                            </div>
                        </div>
                        <div class="form-group">
                            <label class="control-label col-md-3 col-sm-3 col-xs-12">Task</label>
                            <div class="col-md-6 col-sm-6 col-xs-12 has-feedback">
                                <label class="control-label" >{{record.sysProj}}</label>
                            </div>
                        </div>
                        <div class="form-group">
                            <label class="control-label col-md-3 col-sm-3 col-xs-12">Period</label>
                            <div class="col-md-6 col-sm-6 col-xs-12 has-feedback">
                                <label class="control-label" >{{record.period}}</label>
                            </div>
                        </div>

                        <div class="form-group">
                            <label class="control-label col-md-3 col-sm-3 col-xs-12">Addr</label>
                            <div class="col-md-6 col-sm-6 col-xs-12 has-feedback">
                                <label class="control-label" >{{record.addr}}</label>
                            </div>
                        </div>

                        <div class="form-group">
                            <label class="control-label col-md-3 col-sm-3 col-xs-12">Time</label>
                            <div class="col-md-6 col-sm-6 col-xs-12 has-feedback">
                                <label class="control-label" >{{showTime(record.modifiedDate)}}</label>
                            </div>
                        </div>
                        <div class="form-group">
                            <label class="control-label col-md-3 col-sm-3 col-xs-12">Cash({{contract.currency}})</label>
                            <div class="col-md-6 col-sm-6 col-xs-12 has-feedback">
                                <input type="number" 	data-parsley-type="number" v-model="record.cash" id="cash" name="cash"   class="form-control"
                                       required>
                                <span class="fa fa-money form-control-feedback right" aria-hidden="true"></span>
                            </div>
                        </div>
                        <div class="ln_solid"></div>
                        <div class="form-group">
                            <div class="col-md-6 col-sm-6 col-xs-12 col-md-offset-3">
                                <button type="button" class="btn" @click="backList">Back</button>
                                <button type="submit" v-show="method=='new'" class="btn btn-primary" >Save</button>
                                <button type="button" v-show="method=='update'" class="btn btn-danger" @click="delRecord" >Remove</button>
                                <button type="submit" v-show="method=='update'" class="btn btn-primary" >Update</button>
                            </div>
                        </div>
                        <input type="hidden" name="id" :value="record._id" v-if="method=='update'"/>
                    </form>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import moment from 'moment'

export default {
  data () {
    return {
        record:{},
        contract:{},
        method:"new"
    }
  },
  created:function(){
    if (this.$route.params && this.$route.params.recordId)
    {
        var _self=this;
        this.method="update";
        $.post("/project/record/get",{id:this.$route.params.recordId}).done((rs)=>{_self.record=rs.data;_self.contract=rs.contract;}).fail(function(){});
    }
  },
  mounted:function(){
    initFormValidate();
  },
  methods: {
    showTime:function(data)
    {
         return moment(data*1000).format("DD/MM/YYYY, h:mm:ss");
    },
      backList:function()
      {
        this.$router.push('/project/recordList')
      },
    delRecord:function()
    {
        let _self=this;
        confirmOper("Prompt","Confirm delete",function()
        {
                $.post("/project/record/del",{record:_self.record}).done((rs)=>{_self.method="new";_self.record={}; notify("Delete successfully","","success");}).fail(function(){})
        });
    },
    saveRecord:function()
    {
        if (validateForm())
        {
            let _self=this;
            if (this.method=="new")
              $.post("/project/record/save",{record:this.record}).done((rs)=>{_self.method="update"; notify("Saved successfully","","success");}).fail(function(){})
            else
              $.post("/project/record/updateCash",{record:this.record}).done((rs)=>{ notify("Updated successfully","","success");}).fail(function(){})
          }
    }
  }

}
</script>




